import Image from "next/image";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Doctors } from "@/constants";
import { getAppointment } from "@/lib/actions/appointment.actions";
import { formatDateTime } from "@/lib/utils";
import * as Sentry from "@sentry/nextjs";
import { getUser } from "@/lib/actions/patient.actions";
const RequestSuccess = async ({
  searchParams,
  params: {
    userId
  }
}: SearchParamProps) => {
  const appointmentId = searchParams?.appointmentId as string || "";
  const appointment = await getAppointment(appointmentId);
  const doctor = Doctors.find(doctor => doctor.name === appointment.primaryPhysician);
  const user = await getUser(userId);
  Sentry.metrics.set("user_view_appointment-success", user.name);
  return <div className=" flex h-screen max-h-screen px-[5%]" data-sentry-component="RequestSuccess" data-sentry-source-file="page.tsx">
      <div className="success-img">
        <Link href="/" data-sentry-element="Link" data-sentry-source-file="page.tsx">
          <Image src="/medicures.png" height={1000} width={1000} alt="logo" className="h-10 w-fit" data-sentry-element="Image" data-sentry-source-file="page.tsx" />
        </Link>

        <section className="flex flex-col items-center">
          <Image src="/assets/gifs/success.gif" height={300} width={280} alt="success" data-sentry-element="Image" data-sentry-source-file="page.tsx" />
          <h2 className="header mb-6 max-w-[600px] text-center">
            Your <span className="text-green-500">appointment request</span> has
            been successfully submitted!
          </h2>
          <p>We&apos;ll be in touch shortly to confirm.</p>
        </section>

        <section className="request-details">
          <p>Requested appointment details: </p>
          <div className="flex items-center gap-3">
            <Image src={doctor?.image!} alt="doctor" width={100} height={100} className="size-6" data-sentry-element="Image" data-sentry-source-file="page.tsx" />
            <p className="whitespace-nowrap">Dr. {doctor?.name}</p>
          </div>
          <div className="flex gap-2">
            <Image src="/assets/icons/calendar.svg" height={24} width={24} alt="calendar" data-sentry-element="Image" data-sentry-source-file="page.tsx" />
            <p> {formatDateTime(appointment.schedule).dateTime}</p>
          </div>
        </section>

        <Button variant="outline" className="shad-primary-btn" asChild data-sentry-element="Button" data-sentry-source-file="page.tsx">
          <Link href={`/patients/${userId}/new-appointment`} data-sentry-element="Link" data-sentry-source-file="page.tsx">
            New Appointment
          </Link>
        </Button>

        <p className="copyright">Â© 2024 CarePluse</p>
      </div>
    </div>;
};
export default RequestSuccess;